import jjGoodsItem from '../../components/jj-goods-item.vue'
export default {
	data() {
		return {
			
		}
	},
	components:{
		jjGoodsItem
	},
	onLoad() {

	},
	methods: {
		menu(id) {
			uni.navigateTo({
				url:'../goodsList/goodsList?type='+id
			})
		},
		searchPage() {
			uni.navigateTo({
				url:'../search/search'
			})
		},
		event(val){
			if(val=='cszx'){
				uni.navigateTo({
					url:'../information/information'
				})
			} else if(val=='zxfg'){
				uni.navigateTo({
					url:'../statute/statute'
				})
			} else if(val=='zxxt'){
				uni.navigateTo({
					url:'../school/school'
				})
			} else if(val=='lxwm'){
				uni.navigateTo({
					url:'../company_profile/company_profile'
				})
			}
		}
	}
}
